import React, { Component } from "react";
import Fade from "react-reveal";
import ReactDOM from "react-dom";


class Timeline extends Component {
  render() {
    if (!this.props.data) return null;






    return (
      <section id="timeline">
        <Fade duration={1000}>
          <div className="row">

            <div id="container"></div>

          </div>
        </Fade>
      </section>
    );
  }
}

export default Timeline;

