import React, { Component } from "react";
import Fade from "react-reveal";




class Spotify extends Component {
  render() {
    if (!this.props.data) return null;



    const Iframe = ({ source }) => {

      if (!source) {
          return <div>Loading...</div>;
      }

    const src = source;     

    return (
      <section id="spotify">
        <Fade duration={1000}>
          <div className="row">

          <div className="emdeb-responsive">
          <iframe style="border-radius:12px" src={src} width="100%" height="380" frameBorder="0" allowfullscreen="" allow="autoplay; clipboard-write; encrypted-media; fullscreen; picture-in-picture"></iframe>
            </div>

          </div>
        </Fade>
      </section>
    );
  }
}

}
export default Spotify;

